<template>
    <div class="logon">
      <div class="login-img">
<!--        <img src="../../../assets/registerIcon/login_bg.png"/>-->
      </div>
      <div class="upData-input">
        <van-cell-group>
          <van-field v-model="account" placeholder="手机号"  :error-message="inputPhoneError" @input="inputPhone">
          </van-field>
          <van-field ref="code" class="forget-password1" v-show="codeShow"  v-model="code" :placeholder="placeCode" :disabled="disCode"   :error-message="inputCodeError" @input="inputCode"  @click="clickCode">
            <van-button slot="button" class="code" @click.stop="getyzcode" :disabled="attcode" v-if="showbtn">获取验证码</van-button>
            <van-button slot="button" class="code" v-else>{{code_ts}}</van-button>
          </van-field>
        </van-cell-group>
      </div>
<!--      <div class="upBankCard-statement">-->
<!--        <van-checkbox v-model="statement" name="1" checked-color="#FF7100" >已阅读并同意<span style="color: #FF7100">用户隐私协议</span></van-checkbox>-->
<!--      </div>-->
      <van-button class="upBankCard-but" :disabled="!statement" @click="getLogin">登录</van-button>
      <div class="tips" >未注册的手机号验证后自动创建账号</div>
    </div>
</template>

<script>
import { Toast, Notify } from 'vant'
// import { login } from '../../../axios/contentApi'

export default {
  name: 'register',
  data () {
    return {
      // 账号/手机号
      account: '',
      // 短信验证码
      code: '',
      // 短信验证码登录-验证码输入框显示隐藏
      codeShow: true,
      // 登录方式切换
      loginMode: '验证码登录',
      loginModeOff: false,
      // 是否同意声明
      statement: true,
      attcode: true, // 点击获取验证码按钮判断
      showbtn: true, // 展示获取验证码或倒计时按钮判断
      code_ts: '获取验证码', // 倒计时提示文字
      sec: 60, // 倒计时秒数
      websock: null,
      // 存储验证码返回字段
      keying: '',
      // 已注册，直接登录
      signIn: false,
      // 盘口类型 盘口URL
      discUrl:'',
      environment:'',
      webscoketFlag:true,
      inputPhoneError:'',
      inputCodeError:'',
      disCode:true,
      placeCode:'请点击获取短信验证码'
    }
  },
  watch: {
    // 判断手机号是否输入正确
    account: function (value) {
      let reg = /\b1[3456789]\d{9}\b/g
      let val = reg.test(value)
      if (val) {
        this.attcode = false
      } else {
        this.attcode = true
      }
    }
  },
  // 页面销毁时关闭ws连接
  destroyed () {
    //页面销毁时关闭ws连接
    if(this.websock){
      this.websock.close() // 关闭websocket
    }
  },
  created(){
    localStorage.setItem('abnormal','true')
    if(localStorage.getItem('LoginAccount')){
      this.account = localStorage.getItem('LoginAccount')
    }else {
      this.account = this.$route.params.phone
    }
    this.discUrl = '47.74.187.199'
    let remember = JSON.parse(localStorage.getItem('remember'))
    console.log(remember)
    if(remember.environment === "fictitious"){
      this.account = remember.LoginAccount
    }else {
      this.account = this.$route.params.phone
    }
  },
  methods: {
    clickCode(){
      if(this.showbtn && this.code === ''){
        Toast({
          duration:1000,
          position:'bottom',
          message:'请先获取手机验证码',
        })
      }
    },
    inputPhone(){
      let value = this.account
      let reg = /\b1[3456789]\d{9}\b/g
      let val = reg.test(value)
      if (val) {
        this.inputPhoneError = ''
      } else {
        this.inputPhoneError = '手机号格式错误，请重新填写！'
      }
    },
    inputCode(){
      let value = this.code
      let reg = /^\d{5}$/g
      let val = reg.test(value)
      console.log(val)
      if (val) {
        this.inputCodeError = ''
      } else {
        this.inputCodeError = '验证码输入有误，请重新确认！'
      }
    },
    // 获取验证码倒计时
    getyzcode () {
      let that = this
      this.disCode = false
      this.placeCode = '请输入短信验证码'
      this.$axios.get('http://'+this.discUrl+':7777/api/user/smsregister/' + this.account).then(res => {
        // console.log('http://'+this.discUrl+':7777/api/user/smsregister/')
        // console.log('手机号未注册，获取注册验证码')
        if (res.data.code === 0 && res.data.object != null) {
          that.$refs.code.focus()
          Toast({
            duration:1000,
            position:'bottom',
            message:'验证码发送成功',
          })
          // 判断短信成功发送
          this.keying = res.data.object.keying
          let timer = setInterval(() => {
            this.sec = this.sec - 1
            this.code_ts = this.sec + 'S后重试'
            this.showbtn = false
            if (this.sec === 0) {
              clearInterval(timer)
              this.sec = 60
              this.code_ts = this.sec + 'S后重试'
              this.showbtn = true
            }
          }, 1000)
        } else {
          // 短信发送失败，提示手机号已注册
          if (res.data.msg === '手机号已注册') {
            // console.log('手机号已注册，获取登录验证码')
            this.signIn = true
            this.$axios.get('http://'+this.discUrl+':7777/api/user/sms/' + this.account).then(res => {
              // console.log(res)
              if (res.data.code === 0 && res.data.object != null) {
                that.$refs.code.focus()
                Toast({
                  duration:1000,
                  position:'bottom',
                  message:'验证码发送成功'
                })
                this.keying = res.data.object.keying
                let timer = setInterval(() => {
                  this.sec = this.sec - 1
                  this.code_ts = this.sec + 'S后重试'
                  this.showbtn = false
                  if (this.sec === 0) {
                    clearInterval(timer)
                    this.sec = 60
                    this.code_ts = this.sec + 'S后重试'
                    this.showbtn = true
                  }
                }, 1000)
              }
            })
          }else {
            Toast({
              duration:1000,
              position:'bottom',
              message:'验证码发送失败'
            })
          }
          // console.log(res.data.msg)
        }
      })
    },
    getLogin(msg) {
      if(this.code !== ''){
        let that = this;
        console.log(that.global.ws,that.global.ws.readyState)
        if (that.global.ws && that.global.ws.readyState == 1) {
          // console.log("发送信息", msg);
          that.global.ws.onopen = that.onopen()
        }
        that.global.ws.onmessage = function(res) {
          that.websocketonmessage (res)
        };
      }else {
        Toast({
          duration:1000,
          position:'bottom',
          message:'验证码不能为空'
        })
      }

    },
    onopen () { // websocket连接后发送数据(send发送)
      let actions
      let that = this
      if (this.signIn) {
        // console.log(this.signIn)
        actions = {
          action: "login",
          reqID: 4,
          data: {
            loginType:2,
            account: '',
            password: "",
            phone:this.account,
            loginMac: localStorage.uuid,
            client: "Mobile",
            version: localStorage.getInfo,
            keying: this.keying,
            code: this.code,
            reconnection:false,
          }
        } // 请根据实际项目需要进行修改
      } else {
        actions = {
          action: 'register',
          reqID: 1,
          data: {
            phone: this.account,
            keying: this.keying,
            password: "",
            code: this.code,
            is_sim:0
          }
        } // 请根据实际项目需要进行修改
      }
      console.log(actions)
      that.global.ws.send(JSON.stringify(actions))
    },
    websocketonerror () { // 连接建立失败重连
      this.getLogin()
    },
    websocketonmessage (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      console.log(this.redata)
      let _this = this
      if(this.redata.action === "login" || this.redata.action === "register"){
        if (this.redata.data.code === 0) {
          if( _this.redata.data.object !== null){
            localStorage.setItem('uid', _this.redata.data.object.id)
            localStorage.setItem('account',_this.redata.data.object.account)
          }
          localStorage.setItem('Flag', 'isLogin')
          localStorage.setItem('phone', _this.account)
          // localStorage.setItem("disc",this.environment);
          if(!localStorage.remember){
            this.$router.push({
              name:'home',
              params:{
                showRisk:'1'
              }
            })
          }else {
            this.$router.push({
              path:'home'
            })
          }
          localStorage.setItem('LoginAccount', _this.account)
          localStorage.setItem('LoginCode', _this.code)
          localStorage.setItem('LoginKeying', _this.keying)
          localStorage.setItem('LoginMode', 'codeLogin')
          let remember = {}
          remember.LoginAccount = this.account
          remember.environment = localStorage.getItem('environment')
          localStorage.setItem('remember',JSON.stringify(remember))
        }else if(this.redata.data.msg !== null){
          Toast({
            duration:1000,
            position:'bottom',
            message:_this.redata.data.msg
          })
        }
      }
    },
    // websocketsend (Data) { // 数据发送
    //   this.websock.send(Data)
    // },
    websocketclose (e) { // 关闭
      // console.log('断开连接', e)
    },
  }
}
</script>

<style scoped>

</style>



